body, html
{
    background-color:#89ABE3FF;
}
.productImage
{
    max-width: 100%;
    border-radius: 10%;

}
.productsList
{

}
.productShow
{
    background-color: rgb(255,255,255,0.5);
    box-shadow: 0px 5px 20px 5px gray;
    width: 20%;
    margin-left: 1.5%;
    margin-top:2%;
    padding: 1%;
    border-radius: 10px;
    transition: 0.4s linear; 
    display: inline-block;
}
.productShow:hover
{
    background-color: rgb(255,255,255,0.9);
    transition: 0.4s linear;
}
.cartIndicator
{
    color: rgb(59, 55, 55);   
    transition: linear 0.6s;
}
.cartIndicator:hover
{
    color:black;  
    transition: linear 0.6s;
}
.navbar
{
    box-shadow: 0px 2px 0px black;
    background-color:#FCF6F5FF;
}
.addProductButton
{
    margin-top: 2%;
}
